* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

p, h1, h2, h3, ul {
  margin: 0;
  padding: 0;
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

button {
  height: max-content;
  text-align: center;
  color: #fff;
  background-color: #050549;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 18px;
}

button:hover, button:focus {
  cursor: pointer;
  background-color: #2121b8;
}

.search-form {
  width: 100%;
  height: 50px;
  text-align: center;
  z-index: 2;
  background-color: #7499d1;
  margin: 0 0 10px;
  padding: 10px 0;
  position: fixed;
}

.search-form input {
  height: max-content;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 18px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
  padding: 70px 20px 0;
  display: flex;
}

.photo-card {
  border-radius: 4px;
  flex-direction: column;
  flex-basis: calc(25% - 15px);
  justify-content: space-between;
  gap: 20px;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.info {
  justify-content: space-between;
  gap: 30px;
  padding: 0 20px 10px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  font-size: 20px;
  display: flex;
}

.load-more {
  margin: 0 auto 10px;
  display: block;
}

.is-hidden {
  display: none;
}

